@import url(https://fonts.googleapis.com/css?family=PT+Mono|Source+Sans+Pro:600,700&display=swap);
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'PT Mono', monospace;
  padding: 1rem;
  height: 100vh;
  max-width: 24rem;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
h1, h2, h3 {
  font-family: 'Source Sans Pro', sans-serif;
}

a.cta {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 4rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: black;
  border: .125rem solid black;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: .5rem;
  margin: 2rem 0;
}
a.cta:hover {
  background: #000;
  color: #ddd;
}
a.cta:active {
  background: #000;
  color: #FFF;
}


.top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    text-transform: uppercase;
}
.top h1 {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
    text-align: center;
    line-height: 2rem;
    font-size: 2rem;
    margin: 0;
    padding: 1.5rem 0;
}
.top p {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    font-size: .8rem;
    font-weight: 600;
    color: rgba(0,0,0,.4);
    margin: 0;
    padding: .25rem;
}

.card {
    max-width: 24rem;
    height: 14rem;
    border-radius: .6rem;
    background: #eee;
    padding: 1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.4);
            box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.4);
    display: grid;
    gap: .5rem 0;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 2.8rem;
    text-shadow: -1px -1px 2px rgba(255, 255, 255, .4);
}
.evil {
    background: linear-gradient(248.3deg, rgba(0, 0, 0, 0.16) 0%, rgba(0, 0, 0, 0) 100%), #F33355;
    color: white;
}
.blockchain {
    background: linear-gradient(248.52deg, rgba(0, 0, 0, 0.15) 1.49%, rgba(0, 0, 0, 0) 100%), #8B58F9;
    color: white;
}
.bitcoin {
    background: linear-gradient(248.04deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 99.07%), #FFAE34;
    color: #222;
}
.ninja {
    background: linear-gradient(248.3deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 100%), #222222;
    color: white;
}
.blank {
    background: linear-gradient(237.75deg, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 100%), #D0D0D0;
}
.card header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-column: auto / span 2;
    grid-row: auto / span 2;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.card header img {
    opacity: .8;
}
.card header [alt="chip"] {
    -ms-flex-item-align: end;
        align-self: flex-end;
    opacity: 1;
}
.card section.number {
    grid-column: auto / span 2;
    grid-row: auto / span 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.6rem;
    letter-spacing: .05rem;
    padding: .5rem 0 0 0;
    text-transform: uppercase;
}
.card section.info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-column: auto / span 2;
    grid-row: auto / span 1;
}
.card section aside span {
    display: block;
    text-transform: uppercase;
    font-size: .7rem;
    margin: 0 0 .25rem 0;
}
.card section aside.valid span {
    text-align: right;
}
.card section aside p {
    display: block;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
}
.card section aside.valid p {
    text-align: right;
}
.card section.info aside.holder {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}


.card-stack {
    margin: 2rem 0 12rem 0;
    display: grid;
    grid-auto-rows: 4rem;
}


a.cta {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 4rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: black;
  border: .125rem solid black;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: .5rem;
  margin: 2rem 0;
}
a.cta:hover {
  background: #000;
  color: #ddd;
}
a.cta:active {
  background: #000;
  color: #FFF;
}


.card-form {
    margin: 2rem 0 0 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 1rem;
}
.card-form label {
    padding: .25rem 0;
    font-size: .7rem;
    text-transform: uppercase;
}
.card-form input, select {
    border: 1px solid black;
    border-radius: .25rem;
    padding: .5rem;
    height: 2.6rem;
    margin: 0 0 .8rem 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 1rem;
    width: 100%;
}
.col-1 {
    grid-column: auto / span 1;
}
.col-2 {
    grid-column: auto / span 2;
}

